<template>
  <form
    @submit.prevent="onSubmit"
    @reset.prevent="onReset"
    class="form"
    ref="form"
  >
    <!-- secondName -->
    <div class="form__section">
      <h3 class="form__title">Создание клиента</h3>
    </div>
    <div class="form__section">
      <p class="form__banner">Личные данные</p>
      <TextField
        class="form__element"
        :class="$v.form.secondName.$error ? 'form__element_error' : ''"
        name="secondName"
        type="text"
        placeholder="Фамилия *"
        v-model.trim="form.secondName"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.secondName.$dirty && !$v.form.secondName.required"
      >
        Поле обязательно для заполнения
      </div>
      <div
        class="form__error"
        v-if="$v.form.secondName.$dirty && !$v.form.secondName.minLength"
      >
        Фамилия должна иметь как минимум
        {{ $v.form.secondName.$params.minLength.min }} символа
      </div>
      <div
        class="form__error"
        v-if="$v.form.secondName.$dirty && !$v.form.secondName.alpha"
      >
        Фамилия может содержать только буквы
      </div>
    </div>
    <!-- firstName -->
    <div class="form__section">
      <TextField
        class="form__element"
        :class="$v.form.firstName.$error ? 'form__element_error' : ''"
        name="firstName"
        type="text"
        placeholder="Имя *"
        v-model="form.firstName"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.firstName.$dirty && !$v.form.firstName.required"
      >
        Поле обязательно для заполнения
      </div>
      <div class="form__error" v-if="!$v.form.firstName.minLength">
        Имя должно иметь как минимум
        {{ $v.form.firstName.$params.minLength.min }} символа
      </div>
      <div
        class="form__error"
        v-if="$v.form.firstName.$dirty && !$v.form.firstName.alpha"
      >
        Имя может содержать только буквы
      </div>
    </div>
    <!-- lastName -->
    <div class="form__section">
      <TextField
        class="form__element"
        :class="$v.form.lastName.$error ? 'form__element_error' : ''"
        name="lastName"
        type="text"
        placeholder="Отчество"
        v-model.trim="form.lastName"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.lastName.$dirty && !$v.form.lastName.alpha"
      >
        Отчество может содержать только буквы
      </div>
    </div>
    <!-- dateBirth -->
    <div class="form__section">
      <TextField
        class="form__element"
        :class="$v.form.dateBirth.$error ? 'form__element_error' : ''"
        name="dateBirth"
        type="date"
        placeholder="Дата рождения *:"
        v-model.trim="form.dateBirth"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.dateBirth.$dirty && !$v.form.dateBirth.required"
      >
        Поле обязательно для заполнения
      </div>
    </div>
    <!-- gender  -->
    <div class="form__section">
      <RadioInput
        :class="$v.form.gender.$error ? 'form__element_error' : ''"
        name="gender"
        :options="genders"
        @updateField="updateField"
      />
    </div>
    <!-- documentName  -->
    <div class="form__section">
      <SelectorInput
        class="form__element"
        :class="$v.form.documentName.$error ? 'form__element_error' : ''"
        name="documentName"
        placeholder="Документ, удостоверяющий личность *"
        :options="documents"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.documentName.$dirty && !$v.form.documentName.required"
      >
        Поле обязательно для заполнения
      </div>
    </div>
    <!-- documentSeries  -->
    <div class="form__section" v-if="isShowSection">
      <TextField
        class="form__element"
        :class="$v.form.documentSeries.$error ? 'form__element_error' : ''"
        name="documentSeries"
        type="text"
        placeholder="Серия"
        v-model.trim="form.documentSeries"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="
          $v.form.documentSeries.$dirty && !$v.form.documentSeries.maxLength
        "
      >
        Серия документа может содержать максимум
        {{ $v.form.documentSeries.$params.maxLength.max }} символов
      </div>
    </div>
    <!-- documentNumber  -->
    <div class="form__section" v-if="isShowSection">
      <TextField
        class="form__element"
        :class="$v.form.documentNumber.$error ? 'form__element_error' : ''"
        name="documentNumber"
        type="text"
        placeholder="Номер"
        v-model.trim="form.documentNumber"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.documentNumber.$dirty && !$v.form.documentNumber.numeric"
      >
        Номер документа может содержать только цифры
      </div>
      <div
        class="form__error"
        v-if="
          $v.form.documentNumber.$dirty && !$v.form.documentNumber.maxLength
        "
      >
        Номер документа может содержать максимум
        {{ $v.form.documentNumber.$params.maxLength.max }} символов
      </div>
    </div>
    <!-- documentIssued  -->
    <div class="form__section" v-if="isShowSection">
      <TextField
        class="form__element"
        :class="$v.form.documentIssued.$error ? 'form__element_error' : ''"
        name="documentIssued"
        type="text"
        placeholder="Кем выдан"
        v-model.trim="form.documentIssued"
        @updateField="updateField"
      />
    </div>
    <!-- documentDate -->
    <div class="form__section" v-if="isShowSection">
      <TextField
        class="form__element"
        :class="$v.form.documentDate.$error ? 'form__element_error' : ''"
        name="documentDate"
        type="date"
        placeholder="Дата выдачи *:"
        v-model.trim="form.documentDate"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.documentDate.$dirty && !$v.form.documentDate.required"
      >
        Поле обязательно для заполнения
      </div>
    </div>
    <!-- phoneNumber -->
    <div class="form__section">
      <p class="form__banner">Телефон</p>
      <TextField
        class="form__element"
        :class="$v.form.phoneNumber.$error ? 'form__element_error' : ''"
        name="phoneNumber"
        type="text"
        placeholder="Номер телефона *"
        v-model.trim="form.phoneNumber"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.phoneNumber.$dirty && !$v.form.phoneNumber.required"
      >
        Поле обязательно для заполнения
      </div>
      <div
        class="form__error"
        v-if="$v.form.phoneNumber.$dirty && !$v.form.phoneNumber.phone"
      >
        Номер должен начинаться с 7 и содержать 11 цифр
      </div>
    </div>
    <!-- noSentSMS -->
    <div class="form__section">
      <CheckboxInput
        class="form__element"
        :class="$v.form.noSentSMS.$error ? 'form__element_error' : ''"
        name="noSentSMS"
        :text="sendSMS.text"
        :isChecked="sendSMS.isChecked"
        @updateField="updateField"
      />
    </div>
    <!-- index -->
    <div class="form__section">
      <p class="form__banner">Адрес</p>
      <TextField
        class="form__element"
        :class="$v.form.index.$error ? 'form__element_error' : ''"
        name="index"
        type="text"
        placeholder="Индекс"
        v-model.trim="form.index"
        @updateField="updateField"
      />
    </div>
    <!-- country -->
    <div class="form__section">
      <TextField
        class="form__element"
        :class="$v.form.country.$error ? 'form__element_error' : ''"
        name="country"
        type="text"
        placeholder="Страна"
        v-model.trim="form.country"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.country.$dirty && !$v.form.country.alpha"
      >
        Название страны может содержать только буквы
      </div>
    </div>
    <!-- region -->
    <div class="form__section">
      <TextField
        class="form__element"
        :class="$v.form.region.$error ? 'form__element_error' : ''"
        name="region"
        type="text"
        placeholder="Область"
        v-model.trim="form.region"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.region.$dirty && !$v.form.region.alpha"
      >
        Название региона может содержать только буквы
      </div>
    </div>
    <!-- city -->
    <div class="form__section">
      <TextField
        class="form__element"
        :class="$v.form.city.$error ? 'form__element_error' : ''"
        name="city"
        type="text"
        placeholder="Город *"
        v-model.trim="form.city"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.city.$dirty && !$v.form.city.required"
      >
        Поле обязательно для заполнения
      </div>
      <div
        class="form__error"
        v-if="$v.form.city.$dirty && !$v.form.city.alpha"
      >
        Название города может содержать только буквы
      </div>
    </div>
    <!-- street -->
    <div class="form__section">
      <TextField
        class="form__element"
        :class="$v.form.street.$error ? 'form__element_error' : ''"
        name="street"
        type="text"
        placeholder="Улица"
        v-model.trim="form.street"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.street.$dirty && !$v.form.street.alpha"
      >
        Название улицы может содержать только буквы
      </div>
    </div>
    <!-- house -->
    <div class="form__section">
      <TextField
        class="form__element"
        :class="$v.form.house.$error ? 'form__element_error' : ''"
        name="house"
        type="text"
        placeholder="Дом"
        v-model.trim="form.house"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.house.$dirty && !$v.form.house.maxLength"
      >
        Номер дома может содержать максимум
        {{ $v.form.documentSeries.$params.maxLength.max }} символов
      </div>
    </div>
    <!-- customerGroup -->
    <div class="form__section">
      <p class="form__banner">Группа пациента</p>
      <MultiSelectorInput
        class="form__element"
        :class="$v.form.customerGroup.$error ? 'form__element_error' : ''"
        name="customerGroup"
        placeholder="Выберите свою(-и) группу(-ы) *"
        :options="customerGroups"
        @updateField="updateField"
      />
      <div
        class="form__error"
        v-if="$v.form.customerGroup.$dirty && !$v.form.customerGroup.required"
      >
        Поле обязательно для заполнения
      </div>
    </div>
    <!-- doctor -->
    <div class="form__section">
      <p class="form__banner">Лечащий врач</p>
      <SelectorInput
        class="form__element"
        :class="$v.form.doctor.$error ? 'form__element_error' : ''"
        name="doctor"
        placeholder="Выберите лечащего врача"
        :options="doctors"
        @updateField="updateField"
      />
    </div>
    <div class="form__section">
      <p class="form__help">*Поле обязательное для заполнения.</p>
    </div>
    <div class="form__section form__buttons">
      <button class="form__button form__button_reset" type="reset">
        Очистить
      </button>
      <button class="form__button form__button_submit" type="submit">
        Отправить
      </button>
    </div>
  </form>
</template>

<script>
import { validationMixin } from 'vuelidate';
import {
  required,
  minLength,
  maxLength,
  numeric,
  helpers,
} from 'vuelidate/lib/validators';
import TextField from './TextField.vue';
import RadioInput from './RadioInput.vue';
import SelectorInput from './SelectorInput.vue';
import MultiSelectorInput from './MultiSelectorInput.vue';
import CheckboxInput from './CheckboxInput.vue';

const alpha = helpers.regex('alpha', /^[a-zA-Zа-яА-Я]*$/);
const phone = helpers.regex('phone', /^7{1}[0-9]{10}/);

export default {
  name: 'Form',
  components: {
    TextField,
    RadioInput,
    SelectorInput,
    MultiSelectorInput,
    CheckboxInput,
  },
  mixins: [validationMixin],
  data() {
    return {
      // Отправляемые данные
      form: {
        firstName: '',
        secondName: '',
        lastName: '',
        dateBirth: '',
        phoneNumber: '',
        gender: '',
        noSentSMS: false,
        customerGroup: [],
        doctor: '',
        index: '',
        country: '',
        region: '',
        city: '',
        street: '',
        house: '',
        documentName: '',
        documentSeries: '',
        documentNumber: '',
        documentIssued: '',
        documentDate: '',
      },
      // Данные для заполнения
      genders: [
        { value: 'Male', text: 'Мужчина' },
        { value: 'Female', text: 'Женщина' },
      ],
      customerGroups: ['VIP', 'Проблемные', 'ОМС'],
      doctors: ['Иванов', 'Захаров', 'Чернышева'],
      documents: ['Паспорт', 'Свидетельство о рождении', 'Вод. удостоверение'],
      sendSMS: { isChecked: false, text: 'Не отправлять СМС' },
    };
  },
  validations: {
    form: {
      firstName: { required, minLength: minLength(3), alpha },
      secondName: { required, minLength: minLength(3), alpha },
      lastName: { minLength: minLength(3), alpha },
      dateBirth: { required },
      phoneNumber: { required, phone }, //
      gender: {},
      noSentSMS: {},
      customerGroup: { required },
      doctor: {},
      index: {},
      country: { alpha },
      region: { alpha },
      city: { required, alpha },
      street: { alpha },
      house: { maxLength: maxLength(5) },
      documentName: { required },
      documentSeries: { maxLength: maxLength(5) },
      documentNumber: { numeric, maxLength: maxLength(6) },
      documentIssued: {},
      documentDate: { required },
    },
  },
  computed: {
    isShowSection() {
      return this.form.documentName !== '';
    },
  },
  methods: {
    updateField(data) {
      this.form[data.key] = data.value;
    },
    onReset() {
      for (let key in this.form) {
        this.form[key] = '';
      }
      for (
        var i = this.$refs.form.querySelectorAll('input').length - 1;
        i >= 0;
        i--
      ) {
        this.$refs.form.querySelectorAll('input')[i].value = '';
      }
      this.$v.form.$reset();
    },
    onSubmit() {
      this.$v.form.$touch();

      if (!this.$v.form.$error) {
        console.log('Валидация прошла успешно! Данные были отправлены:');

        for (let key in this.form) {
          console.log(`${key}: ${this.form[key]}`);
        }

        // Sending data to the server
        // * magic *

        this.$refs.form.reset();

        alert('Форма была успешно отправлена!');
      }
    },
  },
};
</script>

<style scoped lang="sass">
.form
  min-width: 300px
  display: flex
  flex-direction: column
  font-size: 18px
  padding: 15px
  background: white
  border: 1px solid rgba(0, 0, 0, 0.12)
  box-sizing: border-box
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2)
  border-radius: 4px
  font-family: Helvetica

.form__title
  margin: 0 auto
  padding: 15px 0
  color: rgba(31, 32, 65, 1)

.form__help
  margin: 0 auto
  padding: 15px 0
  color: rgba(31, 32, 65, 0.75)
  font-style: italic
  font-size: 14px

.form__section
  display: flex
  flex-direction: column
  margin-bottom: 10px

.form__wrapper-errors-for-short-inputs
  display: flex
  flex-direction: column
  & .form__element
    width: 100%
    flex-grow: 0

.form__banner
  margin: 0
  line-height: 30px
  color: rgba(31, 32, 65, 1)

.form__element
  flex-grow: 1
  &_error
    border: 1px solid red
    border-radius: 5px

.form__error
  padding-left: 15px
  padding-top: 3px
  font-size: 14px
  color: red

.form__buttons
  display: flex
  flex-direction: row
  justify-content: space-between
  @media screen and (max-device-width: 500px)
    flex-direction: column

.form__button
  width: 200px
  height: 40px
  border: 1px solid rgba(31, 32, 65, 0.75)
  border-radius: 2px
  box-sizing: border-box
  transition-duration: 200ms
  cursor: pointer
  &:hover
    border: 1px solid rgba(31, 32, 65, 1)
  &:focus
    border: 1px solid rgba(31, 32, 65, 1)
    outline: none
  &_submit
    background: #98ff78
    &:hover
      background: #37a814
  &_reset
    &:hover
      background: #c2c2c2
  @media screen and (max-device-width: 500px)
    width: 100%
    &_submit
      margin-top: 10px
</style>
